.category-card {
    box-shadow: rgba(34, 60, 80, 0.2) 4px 4px 8px 0px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 15px;
}

.category-card__wrapper:hover img {
    transform: scale(1.1);
}

.category-card__img {
    overflow: hidden;
}

.category-card__img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 5px;
    transition: all $trans-default;
}

.category-card__text {
    text-align: center;
    font-weight: 600;
}